.facts {
	overflow: hidden;
	background: #F5F3F3;
	img {
		width: 100%;
		height: 100%;
	}
	&__container {
		width: calc((1920px * 795px) / 1728px);
		margin: 0 auto;
		@media (max-width: 1920px){
			width: calc((1920px * 41.41vw) / 1728px);
		}
		@media (max-width: 768px){
			width: 100%;
		}
	}

		&__title {
			width: 100%;
			height: calc((1920px * 76px) / 1728px);
			background: #A8E0D4;
			display: flex;
			justify-content: center;
			align-items: center;
			text-transform: uppercase!important;
			@media (max-width: 1920px){
				height: calc((1920px * 3.96vw) / 1728px);
			}
			@media (max-width: 768px){
				height: 9.500vw;
			}
		}
	
		&__body {
			position: relative;
			@media (max-width: 768px){
				width: 87.500vw;
				margin: 0 auto;
			}
			&--grid {
				position: absolute;
				width: calc((1920px * 198.5px) / 1728px);
				z-index: 6;
				top: calc((1920px * 195.5px) / 1728px);
				border: 1px solid #000000;
				@media (max-width: 1920px){
					width: calc((1920px * 10.35vw) / 1728px);
					top: calc((1920px * 10.24vw) / 1728px);
					border: 0.052vw solid #000000;
				}
				@media (max-width: 768px){
					width: 21.875vw;
					top: 21.875vw;
					border: 0.125vw solid #000000;
				}
				&-left {
					border-right: none;
					left: 0;
					transform: translate(-100%, 0);
				}
			
				&-right {
					border-left: none;
					right: 0;
					transform: translate(100%, 0);
				}
				&--item {
					width: 100%;
					height: calc((1920px * 194px) / 1728px);
					@media (max-width: 1920px){
						height: calc((1920px * 10.104vw) / 1728px);
					}
					@media (max-width: 768px){
						height: 21.875vw;
					}
					&:not(:first-child) {
						border-top: 1px solid #000000;
						@media (max-width: 1920px){
							border-top: 0.052vw solid #000000;
						}
						@media (max-width: 768px){
							border-top: 0.125vw solid #000000;
						}
					}
				}
				>* {
					&:nth-child(1) {
						height: calc((1920px * 197px) / 1728px);
						@media (max-width: 1920px){
							height: calc((1920px * 10.260vw) / 1728px);
						}
						@media (max-width: 768px){
							height: 21.875vw;
						}
					}
					&:nth-child(2) {
						@media (max-width: 768px){
							height: 25.500vw;
						}
					}
					&:nth-child(2) {
						@media (max-width: 768px) {
							height: 18.375vw;
						}
					}
					&:nth-child(4) {
						height: calc((1920px * 195.5px) / 1728px);
						@media (max-width: 1920px) {
							height: calc((1920px * 10.082vw) / 1728px);
						}
						@media (max-width: 768px){
							height: 70vw;
						}
					}
					&:nth-child(5) {
						height: calc((1920px * 193.5px) / 1728px);
						@media (max-width: 1920px){
							height: calc((1920px * 10.078vw) / 1728px);
						}
						@media (max-width: 768px){
							display: none;
						}
					}
				}
			}
		}

